.burger {
    // Fix burger to top right.
    position: fixed;
    right: 0;
    z-index: 1;

    font-size: 36px; // Adjust burger size.
    background-color: $white;
    padding: 8px 16px;
    cursor: pointer;

    // Hide burger menu on large+ devices.
    @include mq(large) {
        display: none;
    }
    
    // Change burger background and line colors on hover.
    &:hover {
        background-color: $grey;
        i {color: black; }
    }
}
